"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { useActiveProjects } from "@/store/useActiveProjects"

export default function LaunchPage() {
  const [buyerWallets, setBuyerWallets] = useState<string[]>([])
  const [totalBuyAmount, setTotalBuyAmount] = useState("1")
  const [tokenInfo, setTokenInfo] = useState({
    name: "Donald Trump",
    symbol: "DNT",
    description: "MAGA DEGEN GOGOGO",
    website: "null",
    telegram: "null",
    twitter: "null",
    address: "CoJUvBkduCa8bUCk4MjvGdczTYpw7MJGJJ77zUFuKQ",
    ipfs: "https://ipfs.io/ipfs/QmadiqcFdy4KGD4FsbNmazAwauiNowjebGYdeUfA6LPdM",
  })
  const [fundingWallet, setFundingWallet] = useState(
    "Balance: 0.006002000 SOL (1.24 USDT) - G92h3wp62bHyqoGyXR8fgiXUkLFvwWFHvHKtokApBCxq"
  )
  const [devWallet, setDevWallet] = useState("Random address")

  const { addProject } = useActiveProjects()

  const addWallet = () => {
    setBuyerWallets([...buyerWallets, ""])
  }

  const deleteWallet = (index: number) => {
    const newWallets = buyerWallets.filter((_, i) => i !== index)
    setBuyerWallets(newWallets)
  }

  const equalBuy = () => {
    if (buyerWallets.length === 0) return
    const amount = parseFloat(totalBuyAmount) / buyerWallets.length
    const newWallets = buyerWallets.map(() => amount.toFixed(8))
    setBuyerWallets(newWallets)
  }

  const randomBuy = () => {
    if (buyerWallets.length === 0) return
    const total = parseFloat(totalBuyAmount)
    let remaining = total
    const newWallets = buyerWallets.map((_, index) => {
      if (index === buyerWallets.length - 1) {
        return remaining.toFixed(8)
      }
      const amount = Math.random() * (remaining / 2)
      remaining -= amount
      return amount.toFixed(8)
    })
    setBuyerWallets(newWallets)
  }

  const handleLaunch = () => {
    addProject({ id: tokenInfo.address, name: tokenInfo.name })
    alert(`Token "${tokenInfo.name}" launched and added to active projects!`)
  }

  return (
    <div className="h-screen p-2 overflow-y-auto">
      <div className="grid grid-cols-2 gap-4">
        {/* Left Column */}
        <div className="space-y-4">
          {/* Token Selection Card */}
          <Card className="p-3 border-2 border-blue-500 rounded-xl bg-slate-800">
            <h2 className="text-lg font-semibold mb-2 text-gray-100">1️⃣ Select Token to Launch</h2>
            <div className="space-y-2">
              <input
                type="text"
                value={tokenInfo.name}
                onChange={(e) => setTokenInfo({ ...tokenInfo, name: e.target.value })}
                placeholder="Name"
                className="w-full p-2 border border-gray-700 rounded-lg bg-slate-900 text-gray-100 focus:border-blue-500 focus:ring-1 focus:ring-blue-500"
              />
              <input
                type="text"
                value={tokenInfo.symbol}
                onChange={(e) => setTokenInfo({ ...tokenInfo, symbol: e.target.value })}
                placeholder="Symbol"
                className="w-full p-2 border border-gray-700 rounded-lg bg-slate-900 text-gray-100 focus:border-blue-500 focus:ring-1 focus:ring-blue-500"
              />
              <textarea
                value={tokenInfo.description}
                onChange={(e) => setTokenInfo({ ...tokenInfo, description: e.target.value })}
                placeholder="Description"
                className="w-full p-2 border border-gray-700 rounded-lg bg-slate-900 text-gray-100 focus:border-blue-500 focus:ring-1 focus:ring-blue-500"
              />
              <input
                type="text"
                value={tokenInfo.ipfs}
                onChange={(e) => setTokenInfo({ ...tokenInfo, ipfs: e.target.value })}
                placeholder="IPFS Link"
                className="w-full p-2 border border-gray-700 rounded-lg bg-slate-900 text-gray-100 focus:border-blue-500 focus:ring-1 focus:ring-blue-500"
              />
              <input
                type="text"
                value={tokenInfo.address}
                onChange={(e) => setTokenInfo({ ...tokenInfo, address: e.target.value })}
                placeholder="Address"
                className="w-full p-2 border border-gray-700 rounded-lg bg-slate-900 text-gray-100 focus:border-blue-500 focus:ring-1 focus:ring-blue-500"
              />
            </div>
          </Card>

          {/* Funding Wallet Card */}
          <Card className="p-3 border-2 border-blue-500 rounded-xl bg-slate-800">
            <h2 className="text-lg font-semibold mb-2 text-gray-100">2️⃣ Select Funding Wallet</h2>
            <select
              value={fundingWallet}
              onChange={(e) => setFundingWallet(e.target.value)}
              className="w-full p-2 border border-gray-700 rounded-lg bg-slate-900 text-gray-100 hover:border-blue-500 focus:border-blue-500 focus:ring-1 focus:ring-blue-500 transition-colors cursor-pointer text-sm"
            >
              <option>Balance: 0.006002000 SOL (1.24 USDT) - G92h3wp62bHyqoGyXR8fgiXUkLFvwWFHvHKtokApBCxq</option>
            </select>
          </Card>

          {/* Dev Wallet Card */}
          <Card className="p-3 border-2 border-blue-500 rounded-xl bg-slate-800">
            <h2 className="text-lg font-semibold mb-2 text-gray-100">3️⃣ Select Dev Wallet</h2>
            <input
              type="text"
              value={devWallet}
              onChange={(e) => setDevWallet(e.target.value)}
              placeholder="Dev address"
              className="w-full p-2 border border-gray-700 rounded-lg bg-slate-900 text-gray-100 focus:border-blue-500 focus:ring-1 focus:ring-blue-500"
            />
          </Card>
        </div>

        {/* Right Column */}
        <div className="space-y-4">
          {/* Buyer Wallets Card */}
          <Card className="p-3 border-2 border-blue-500 rounded-xl bg-slate-800">
            <h2 className="text-lg font-semibold mb-2 text-gray-100">4️⃣ Add Buyer Wallets</h2>
            <div className="space-y-2">
              <div className="flex gap-2 items-center">
                <span className="text-sm text-gray-400">Total buy amount (SOL)</span>
                <Input
                  value={totalBuyAmount}
                  onChange={(e) => setTotalBuyAmount(e.target.value)}
                  className="max-w-[150px] bg-slate-900 border-gray-700 text-gray-100 hover:border-blue-500 focus:border-blue-500 focus:ring-1 focus:ring-blue-500 transition-colors text-sm h-8"
                />
              </div>

              <div className="flex flex-wrap gap-1.5">
                <Button
                  onClick={addWallet}
                  variant="default"
                  className="bg-blue-500 hover:bg-blue-600 text-white text-sm h-8 px-3"
                >
                  Add wallet
                </Button>
                <Button
                  onClick={() => setBuyerWallets([])}
                  variant="default"
                  className="bg-blue-500 hover:bg-blue-600 text-white text-sm h-8 px-3"
                >
                  Delete wallet
                </Button>
                <Button
                  onClick={equalBuy}
                  variant="secondary"
                  className="bg-gray-700 hover:bg-gray-600 text-white text-sm h-8 px-3"
                >
                  Equal buy
                </Button>
                <Button
                  onClick={randomBuy}
                  variant="secondary"
                  className="bg-gray-700 hover:bg-gray-600 text-white text-sm h-8 px-3"
                >
                  Random buy
                </Button>
              </div>

              <div className="space-y-1.5 max-h-[200px] overflow-y-auto pr-2">
                {buyerWallets.map((wallet, index) => (
                  <div key={index} className="flex gap-1.5 items-center">
                    <span className="w-6 text-gray-400 text-sm">{index + 1}</span>
                    <Input
                      value={wallet}
                      onChange={(e) => {
                        const newWallets = [...buyerWallets]
                        newWallets[index] = e.target.value
                        setBuyerWallets(newWallets)
                      }}
                      className="flex-1 bg-slate-900 border-gray-700 text-gray-100 hover:border-blue-500 focus:border-blue-500 focus:ring-1 focus:ring-blue-500 transition-colors text-sm h-8"
                    />
                    <Button
                      onClick={() => deleteWallet(index)}
                      variant="ghost"
                      className="hover:bg-red-500/10 hover:text-red-500 h-8 w-8 p-0"
                    >
                      ×
                    </Button>
                  </div>
                ))}
              </div>
            </div>
          </Card>

          {/* Confirm and Launch Card */}
          <Card className="p-3 border-2 border-blue-500 rounded-xl bg-slate-800">
            <h2 className="text-lg font-semibold mb-2 text-gray-100">5️⃣ Confirm and Launch</h2>
            <Button
              onClick={handleLaunch}
              variant="default"
              className="bg-blue-600 hover:bg-blue-700 text-white text-lg w-full py-3"
            >
              LAUNCH
            </Button>
          </Card>
        </div>
      </div>
    </div>
  )
}
